<template>
	<div class="edit_form">
		<el-form :model="ruleFormDetail" :rules="rulesDetail" ref="ruleFormDetail" label-width="100px">
			<el-row>
				<el-col :span="12">
					<el-form-item label="Title" prop="title">
						<el-input v-model="ruleFormDetail.title" placeholder=""></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="" prop="version">
						<el-input v-model="ruleFormDetail.version" placeholder=""></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="12">
					<el-form-item label="" prop="description">
						<el-input v-model="ruleFormDetail.description" placeholder=""></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="" prop="description">
						<el-input v-model="ruleFormDetail.description" placeholder=""></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="12">
					<el-form-item label="" prop="force">
						<el-select v-model="ruleFormDetail.force" placeholder="">
							<el-option label="" :value="1"></el-option>
							<el-option label="" :value="0"></el-option>
						</el-select>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="" prop="status">
						<el-select v-model="ruleFormDetail.status" placeholder="">
							<el-option label="" :value="1"></el-option>
							<el-option label="" :value="0"></el-option>
						</el-select>
					</el-form-item>
				</el-col>
			</el-row>
			<el-form-item label="" prop="description">
				<el-input v-model="ruleFormDetail.description" placeholder=""></el-input>
			</el-form-item>
			<el-row>
				<el-col :span="12">
					<el-form-item label="" prop="size">
						<el-input v-model="ruleFormDetail.size" placeholder=""></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="url" prop="download_url_object">
						<el-input v-model="ruleFormDetail.download_url_object" placeholder=""></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="12">
					<el-form-item label="url" prop="img_url_object">
						<el-input v-model="ruleFormDetail.img_url_object" placeholder=""></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="" prop="release_tm">
						<el-input v-model="ruleFormDetail.release_tm" placeholder=""></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="12">
					<el-form-item label="md5" prop="download_file_md5">
						<el-input v-model="ruleFormDetail.download_file_md5" placeholder=""></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="" prop="created_at">
						<el-input v-model="ruleFormDetail.created_at" placeholder=""></el-input>
					</el-form-item>
				</el-col>
			</el-row>
			<el-row>
				<el-col :span="12">
					<el-form-item label="" prop="updated_at">
						<el-input v-model="ruleFormDetail.updated_at" placeholder=""></el-input>
					</el-form-item>
				</el-col>
				<el-col :span="12">
					<el-form-item label="" prop="delete_at">
						<el-input v-model="ruleFormDetail.delete_at" placeholder=""></el-input>
					</el-form-item>
				</el-col>
			</el-row>
		</el-form>
	</div>
</template>
<script>
import * as namespace from "../../../store/namespace";
import { mapGetters, mapActions } from "vuex";
export default {
    props: ["ruleFormDetail"],
    data() {
        return {
            uploadObj: {
                token: JSON.parse(localStorage.getItem("localData")).user.info.token
            },
            importForm: {
                title: "",
                version: "",
                release_tm: "",
                routersList: [],
                productsList: [],
                download_url_object: "",
                img_url_object: "",
                download_file_md5: "",
                file_size: "",
                force: 0,
                brand_id: "",
                type_id: "",
                product_id: "",
                description: "",
                note: ""
            },
            rulesImport: {
                version: [{ required: true, message: "" }],
                title: [{ required: true, message: "" }],
                description: [{ required: true, message: "" }],
                note: [{ required: true, message: "" }],
                product_id: [{ required: false, message: "" }],
                release_tm: [{ required: true, message: "" }],
                routersList: [{ required: false, message: "" }],
                productsList: [{ required: false, message: "" }],
                download_url_object: [{ required: true, message: "" }],
                img_url_object: [{ required: true, message: "img" }],
                force: [{ required: true, message: "" }]
            }
        };
    },
    mounted() {},
    methods: {
        resetImportForm() {
            let form = this.importForm;
            for (let attr in form) {
                switch (attr) {
                    case "routersList":
                    case "productsList":
                        form[attr] = [];
                        break;
                    case "force":
                        form[attr] = 0;
                        break;
                    default:
                        form[attr] = "";
                        break;
                }
            }
        },
        closeParentFlow() {
            this.$emit("closeImportBox");
        }
    },
    computed: {
        ...mapGetters({
            subset: namespace.SUBSET
        })
    }
};
</script>
<style lang="less">
.edit_form {
    .el-form-item__label {
        width: 80px;
    }
    .el-select {
        width: 100%;
    }
}
.el-input.is-disabled .el-input__inner {
    color: #333;
}
</style>



